import React, { Component } from "react";
import PropTypes from 'prop-types';

import { checkIsMerchantAddress, getMerchantByAddress } from 'utils/merchantUtils';


class AddressName extends Component {
  render() {
    const { address } = this.props;
    const isMerchantAddress = checkIsMerchantAddress(address);
    if (isMerchantAddress) {
      const merchant = getMerchantByAddress(address);
      const img = ((!!merchant.logo_file_name) ?
        <img className="address-name-logo" src={ 'https://raw.githubusercontent.com/self-token/global-data/master/merchant_logos/' + merchant.logo_file_name } alt="merchant logo" /> :
        null
      );
      // const logo = require(imgPath);
      return <div className="h5 font-weight-light mt-2 text-white">
        { img }
        <p className="mt-2">{ merchant.name_beside_logo }</p>
      </div>
    }
    return null;
  }
}

AddressName.propTypes = {
  address: PropTypes.string,
};

AddressName.defaultProps = {
  address: '0x0000000000000000000000000000000000000000',
};

export default AddressName;
