import React, { Component } from 'react';
import PropTypes from 'prop-types';
// import NumericInput from 'react-numeric-input';
// import NumericInput from 'components/react-numeric-input/index';
// import Modal from 'react-modal';
import { Modal, Form } from 'react-bootstrap';
import qs from 'qs';
import Cookies from 'universal-cookie';

import QrcodeImg from 'components/QrcodeImg.js';


function isNumeric(num){
  return !isNaN(num);
}

const currencyList = ['ntd', 'self', 'none'];
const currencySet = new Set(currencyList);
const cookies = new Cookies();

class MerchantReceiveToken extends Component {
  state = {
    baseUrl: "https://selfer.selftoken.co/",
    isSelfAmountDesignated: true,
    isShowingPayWithDappBrowserModal: false,
    isShowingPayWithAnyWalletModal: false,
    isTesting: false,
    selfAmount: 100,
    selfAmountInputValue: "100",
    isValidSelfAmountInputValue: true,
    ntdAmount: 300,
    ntdAmountInputValue: 300,
    isValidNtdAmountInputValue: true,
    selectedCurrency: 'ntd', // 'ntd', 'self', 'none'
    isShowingMoreInfoForPayingWithDappBrowser: false,
  }

  componentDidMount = async () => {
    await this.loadSelectedCurrencyFromCookies();

    // change baseUrl to window.location.origin if is testing
    try {
      console.log('MerchantReceiveToken this.props.location', this.props.location);
      const queries = qs.parse(this.props.location.search, { ignoreQueryPrefix: true });
      if (!!queries.testing) {
        const baseUrl = window.location.origin + '/#/';
        this.setState({
          isTesting: true,
          baseUrl,
        });
        console.log('is testing. Change baseUrl to ' + baseUrl);
      }
    } catch (error) {
      console.error('MerchantReceiveToken componentDidMount ', error);
    }
  }

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  // handleAmountChange = (value) => {
  //   this.setState({
  //     selfAmount: value,
  //   });
  // }

  // handleNumericInputFocus = (event) => {
  //   if (event.target.select) {
  //     event.target.select();
  //   }
  // }

  loadSelectedCurrencyFromCookies = async () => {
    const selectedCurrency = cookies.get('selectedCurrency');
    if (currencySet.has(selectedCurrency)) {
      this.setState({
        selectedCurrency,
        isSelfAmountDesignated: (selectedCurrency !== 'none'),
      });
    }
  }

  saveSelectedCurrencyToCookies = async (selectedCurrency) => {
    cookies.set('selectedCurrency', selectedCurrency, { path: '/' });
  }

  handleSelfAmountSubmit = (event) => {
    event.preventDefault();
    if (this.state.isValidSelfAmountInputValue) {
      this.showPayWithDappBrowserModal();
    }
  }

  handleSelfAmountInputChange = (event) => {
    console.log('handleSelfAmountInputChange', event);

    // will be empty string when number is not valid
    const selfAmountInputValue = event.target.value;

    // if is empty string
    if (!selfAmountInputValue) {
      this.setState({
        // selfAmountInputValue: '',
        selfAmount: 0,
        isValidSelfAmountInputValue: false,
        // ntdAmountInputValue: '',
        ntdAmount: 0,
        // isValidNtdAmountInputValue: false,
      });
    }

    const selfAmount = Number(selfAmountInputValue);
    const isValidSelfAmountInputValue = isNumeric(selfAmount);
    if (!isValidSelfAmountInputValue) {
      this.setState({
        // selfAmountInputValue: '',
        selfAmount: 0,
        isValidSelfAmountInputValue: false,
        // ntdAmountInputValue: '',
        ntdAmount: 0,
        // isValidNtdAmountInputValue: false,
      });
    }

    const ntdAmount = selfAmount * 3;
    const ntdAmountInputValue = ntdAmount.toString();
    this.setState({
      selfAmountInputValue,
      selfAmount,
      isValidSelfAmountInputValue: true,
      ntdAmountInputValue,
      ntdAmount,
      isValidNtdAmountInputValue: true,
    });
  }

  handleNtdAmountInputChange = (event) => {
    console.log('handleNtdAmountInputChange', event);

    // will be empty string when number is not valid
    const ntdAmountInputValue = event.target.value;

    // if is empty string
    if (!ntdAmountInputValue) {
      this.setState({
        // selfAmountInputValue: '',
        selfAmount: 0,
        // isValidSelfAmountInputValue: false,
        // ntdAmountInputValue: '',
        ntdAmount: 0,
        isValidNtdAmountInputValue: false,
      });
    }

    const ntdAmount = Number(ntdAmountInputValue);
    const isValidNtdAmountInputValue = isNumeric(ntdAmount);
    if (!isValidNtdAmountInputValue) {
      this.setState({
        // selfAmountInputValue: '',
        selfAmount: 0,
        // isValidSelfAmountInputValue: false,
        // ntdAmountInputValue: '',
        ntdAmount: 0,
        isValidNtdAmountInputValue: false,
      });
    }

    const selfAmount = Math.round(ntdAmount / 3);
    const selfAmountInputValue = selfAmount.toString();
    this.setState({
      selfAmountInputValue,
      selfAmount,
      isValidSelfAmountInputValue: true,
      ntdAmountInputValue,
      ntdAmount,
      isValidNtdAmountInputValue: true
    });
  }

  selectEventTarget = (event) => {
    if (event.target.select) {
      event.target.select();
    }
  }

  computeUrl = () => {
    const { accounts } = this.props;

    if (accounts.length === 0) {
      return "";
    }

    const { baseUrl, isSelfAmountDesignated, selfAmount, isTesting } = this.state;

    return baseUrl + (isTesting ? 'transfer' : 'trans')
    + "?to=" + accounts[0]
    + ( (isSelfAmountDesignated && !!selfAmount) ? ("&self=" + selfAmount.toString()) : "" );
  }

  showPayWithDappBrowserModal = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: true,
      isShowingPayWithAnyWalletModal: false,
    });
  }

  showPayWithAnyWalletModal = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: false,
      isShowingPayWithAnyWalletModal: true,
    });
  }

  closeModals = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: false,
      isShowingPayWithAnyWalletModal: false,
    });
  }

  handleSelectedCurrencyChange = (event) => {
    const selectedCurrency = event.target.value;
    this.setState({
      selectedCurrency,
      isSelfAmountDesignated: (selectedCurrency !== 'none'),
    });
    this.saveSelectedCurrencyToCookies(selectedCurrency).then();
  }

  toggleShowingMoreInfoForPayingWithDappBrowser = () => {
    this.setState({
      isShowingMoreInfoForPayingWithDappBrowser: !this.state.isShowingMoreInfoForPayingWithDappBrowser
    });
  }

  render() {
    const { strings, accounts } = this.props;

    if (accounts.length === 0) {
      return <div>
        <div className="alert alert-danger">{ strings.msg_no_account }</div>
      </div>;
    }

    const {
      isSelfAmountDesignated,
      selfAmount,
      selfAmountInputValue,
      isValidSelfAmountInputValue,
      // ntdAmount,
      ntdAmountInputValue,
      isValidNtdAmountInputValue,
      selectedCurrency,
      isShowingMoreInfoForPayingWithDappBrowser,
    } = this.state;
    const selfDappUrl = this.computeUrl();

    return (
      <>
        {/* PayWithDappBrowserModal */}
        <Modal
          show={this.state.isShowingPayWithDappBrowserModal}
          onHide={this.closeModals}
          centered={ true }
        >
          {/* <Modal.Header style={{ padding: "0.5rem" }} closeButton></Modal.Header> */}
          <Modal.Header></Modal.Header>
          <Modal.Body style={{padding: "0 1rem"}}>
            <div className="text-center">
              <div className="h5 mb-2">{ strings.Pay_with_DApp_Browser }</div>

              <div className="mb-1">
                <div>{ strings.msg_please_scan_with_any_qr_code_scanner_1 }</div>
                <div>{ strings.msg_please_scan_with_any_qr_code_scanner_2 }</div>
                <div>{ strings.msg_to_pay } <span className="text-white">{ isSelfAmountDesignated ? selfAmount : '' }</span> SELF{ strings.punc_period }</div>
              </div>

              <div className="mb-1">
                <QrcodeImg text={ selfDappUrl } width={ 320 } margin={ 2 } />
              </div>

              { !isShowingMoreInfoForPayingWithDappBrowser &&
                <div className="clickable text-additional text-underlined" onClick={ this.toggleShowingMoreInfoForPayingWithDappBrowser }>More Info</div>
              }
              { isShowingMoreInfoForPayingWithDappBrowser &&
                <div>
                  <div className="mb-1 text-additional">
                    <div>{ strings.msg_supported_dapp_browsers_1 }</div>
                    <div>{ strings.msg_supported_dapp_browsers_2 }</div>
                  </div>
                  <div className="mb-1 text-additional">目前 EvenToken 尚不支援此種付款方式，如欲使用 EvenToken 支付 SELF 請改用「使用任何錢包支付」的方式。</div>
                  <div className="mb-1 text-white text-additional word-wrap-break-word">QR Code URL: { selfDappUrl }</div>
                </div>
              }
              { isShowingMoreInfoForPayingWithDappBrowser &&
                <div className="clickable text-additional text-underlined" onClick={ this.toggleShowingMoreInfoForPayingWithDappBrowser }>Less Info</div>
              }
            </div>

            <div className="text-right mt-3 mb-3">
              <span className="clickable" onClick={ this.showPayWithAnyWalletModal }>
                { strings.Pay_with_Any_Wallet + " " } <i className="fas fa-caret-right" />
              </span>
            </div>
          </Modal.Body>
        </Modal>

        {/* PayWithAnyWalletModal */}
        <Modal
          show={this.state.isShowingPayWithAnyWalletModal}
          onHide={this.closeModals}
          centered
        >
          {/* <Modal.Header style={{ padding: "0.5rem" }} closeButton></Modal.Header> */}
          <Modal.Header></Modal.Header>
          <Modal.Body style={{ padding: "0 1rem" }}>
            <div className="text-center">
              <div className="h5 mb-2">{ strings.Pay_with_Any_Wallet }</div>

              <div className="mb-1">
                <div>
                  <span>{ strings.msg_please_send_self_1 }</span>
                  <span className="text-white"> { isSelfAmountDesignated ? selfAmount : '' } SELF </span>
                  <span>{ strings.msg_please_send_self_2 }</span>
                </div>
                <div>
                  <span>({ strings.msg_need_eth_for_gas_fee })</span>
                </div>
              </div>

              <div className="mb-1">
                <QrcodeImg text={ accounts[0] } width={ 320 } margin={ 2 } />
              </div>

              <div className="mb-1 text-additional word-wrap-break-word text-white">{ accounts[0] }</div>
            </div>

            <div className="text-left mt-3 mb-3">
              <span className="clickable" onClick={ this.showPayWithDappBrowserModal }><i className="fas fa-caret-left" /> { strings.Pay_with_DApp_Browser }</span>
            </div>
          </Modal.Body>
        </Modal>

        <div className="text-center">
          <div className="mb-1">
            <Form.Control as="select"
              className="merchant-currency-selector"
              value={selectedCurrency}
              onChange={this.handleSelectedCurrencyChange}
            >
              <option value="ntd">{ strings.NTD }</option>
              <option value="self">{ strings.EN_SELF }</option>
              <option value="none">{ strings.unlimited_payment_amount }</option>
            </Form.Control>
          </div>

          <div className="mb-3">
            <form onSubmit={this.handleSelfAmountSubmit} noValidate>
              { selectedCurrency === "self" && <>
                <input
                  type="number"
                  className={
                    "numeric-input "
                    + (isValidSelfAmountInputValue ? "" : "not-valid-input ")
                  }
                  value={ selfAmountInputValue }
                  // onChange={this.handleSelfAmountInputChange}
                  onInput={this.handleSelfAmountInputChange}
                  onFocus={this.selectEventTarget}
                />
              </>}
              { selectedCurrency === "ntd" && <>
                <input
                  type="number"
                  className={
                    "numeric-input mb-1 "
                    + (isValidNtdAmountInputValue ? "" : "not-valid-input ")
                  }
                  value={ ntdAmountInputValue }
                  // onChange={this.handleSelfAmountInputChange}
                  onInput={this.handleNtdAmountInputChange}
                  onFocus={this.selectEventTarget}
                />
                <p>= {selfAmount} SELF</p>
              </>}
            </form>
          </div>

          <div className="mb-3">
            <button className="solid-rectangle-button bg-golden" onClick={ this.showPayWithDappBrowserModal }>{ strings.Receive_Payment }</button>
            {/* <h6>{ strings.Payment_Method }</h6>
            <button className="solid-rectangle-button bg-golden m-2" onClick={ this.showPayWithDappBrowserModal }>{ strings.DApp_Browser }</button>
            <button className="solid-rectangle-button bg-golden m-2" onClick={ this.showPayWithAnyWalletModal }>{ strings.Any_Wallet }</button> */}
          </div>
        </div>
      </>
    );
  }
}

MerchantReceiveToken.propTypes = {
  accounts: PropTypes.array,
  strings: PropTypes.object,
};

MerchantReceiveToken.defaultProps = {
  accounts: [],
  strings: {},
};

export default MerchantReceiveToken;
