import merchants from 'data/merchants';

const addressToMerchant = {};
for (const merchant of merchants) {
  addressToMerchant[merchant.address.toLowerCase()] = merchant;
}

export const getMerchantByAddress = (address) => {
  const lowerCaseAddress = address.toLowerCase();
  if (addressToMerchant.hasOwnProperty(lowerCaseAddress)) {
    return addressToMerchant[lowerCaseAddress];
  }
  return null;
}

export const checkIsMerchantAddress = (address) => {
  const lowerCaseAddress = address.toLowerCase();
  return addressToMerchant.hasOwnProperty(lowerCaseAddress);
}
