import React, { Component } from "react";
import PropTypes from 'prop-types';
import BigNumber from 'utils/BigNumber';
import { formatWeiToEther } from 'utils/formatData';

import selftokenIconCrop from "assets/img/selftoken_transparent_crop.png";


class MerchantBalances extends Component {
  render() {
    const { strings, selfBalanceBN } = this.props;

    const selfBalanceStr = formatWeiToEther(selfBalanceBN, 2);

    // icon floats left and text floats right when screen width <= 767px
    // icon and text in the same line when screen width >= 768px
    return (
      <div className="row">
        <div className="col-12 text-center mb-2">
          <div className="h4 balance-container">
            <span className="h3 mb-0 mr-3 balance-icon-container">
              <img
                src={selftokenIconCrop}
                alt="self-token-icon"
                style={{height: "1em"}}
              />
            </span>
            <span className="balance-text-container">{ selfBalanceStr } { strings.EN_SELF }</span>
          </div>
        </div>
      </div>
    );
  }
}

MerchantBalances.propTypes = {
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
};

MerchantBalances.defaultProps = {
  strings: {},
  selfBalanceBN: new BigNumber(0),
};

export default MerchantBalances;
